import Vue from 'vue'
import Router from 'vue-router'
// 根
const index = ()=>import("@/view/erpStandardVersion/pages/index/index");
// 楼盘管理
const buildManager = ()=>import("@/view/erpStandardVersion/pages/buildManager/buildManager");
const actProject = ()=>import("@/view/erpStandardVersion/pages/buildManager/actProject");
const newHouseStore = ()=>import("@/view/erpStandardVersion/pages/buildManager/newHouseStore");

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "index",
      component: index,
      redirect: "/buildManager",
      children: [
        {
          path: "/buildManager",
          name: "buildManager",
          component: buildManager,
          redirect: "/buildManager/actProject",
          children: [
            {
              path: "actProject",
              name: "actProject",
              component: actProject,
            },
            {
              path: "newHouseStore",
              name: "newHouseStore",
              component: newHouseStore
            }
          ]
        }
      ]
    }
  ]
})



